export const ProcessingStatusIds = StatusList.map((status) =>
  ['In Queue', 'Processing'].includes(status.description) ? status.id : null
).filter((id) => id !== null)

export const SuccessStatusIds = StatusList.map((status) =>
  ['Accepted'].includes(status.description) ? status.id : null
).filter((id) => id !== null)
